___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  border: 0;
  padding: 0;
  font-family: "Bitter", serif;
}

ion-content {
  --background: linear-gradient(to top, #33cccc 0%, #006666 100%);
}

ion-toolbar {
  --background: none;
  color: #fff;
  text-transform: uppercase;
}

#container {
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  padding-right: 10px;
  padding-left: 10px;
}

ion-button {
  margin-top: 20px;
}

ion-icon {
  color: rgb(255, 255, 255);
  margin: 10px;
}

h1 {
  color: #fff;
  margin-bottom: 70px;
}

ion-list {
  --ion-item-background: transparent;
  margin: 0 20px;
}

ion-list ion-item {
  margin-bottom: 15px;
  --border-color: #004b4d;
}

ion-button[size=full]:hover {
  color: #fff;
  transition: all 300ms ease-in-out;
}

ion-input {
  font-size: 15px;
}

#botao-cadastro {
  margin-top: 15%;
  padding-left: 20px;
  padding-right: 20px;
  text-transform: uppercase;
  font-weight: 300;
  color: #fff;
  --background: #004b4d;
